button {
  border: none;
  width: 150px;
  padding: 10px 15px;
  background-color: teal;
  color: #fff;
  font-weight: 500;
  margin-left: 5px;
  margin-right: 5px;
  font-family: inherit;
  cursor: pointer;
  outline: none;
  box-shadow: 1px 2px 2px 0 rgb(0 0 0 / 0.5);
  transition: box-shadow 250ms linear;
}

.item:not(:last-child) {
  margin-right: 10px;
}
.item {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  margin-bottom: 20px;
}
.good {
  background-color: green;
}

.neutral {
  background-color: orange;
}

.bad {
  background-color: red;
}
